@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;1,600;1,700&family=Roboto+Mono:ital,wght@0,100;0,300;0,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;1,600;1,700&display=swap');*/
.App {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: #38516A;
    text-align: center;
    background-color: #EDEDE9;
    font-family: 'Playfair Display', serif;
}

.Scroll-Container{display:flex;justify-content:center;align-items:center;flex-flow:column wrap}.section{text-align:center}.section img{width:50%;padding:5% 5% 1%}.two-columns img{width:40%}.first{background-image:linear-gradient(#EDEDE9, #CCDEE7);color:#38516A}.second{background-image:linear-gradient(#CCDEE7, #EBD7C9);color:#90664F}.third{background-image:linear-gradient(#EBD7C9, #C4B99B);color:#90664F}.fourth{background-image:linear-gradient(#C4B99B, #90664F);color:#EBD7C9}.fifth{background-image:linear-gradient(#90664F, #38516A);color:#CCDEE7}.sixth{background-image:linear-gradient(#38516A, black);color:#EDEDE9}

